li {
	float: left;
	/* padding: 30px; */
}

nav ul {
	list-style-type: none;
	color: white;
	background: black;
	overflow: hidden;
}
li a {
	text-decoration: none;
	color: white;
}

.active {
	background-color: rgb(157, 162, 158);
}

#menu_icon_li{
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}
#menu_li, #footer_li {
	text-align: right;
	display: flex;
	float: right;
}

.menu,.footer-item {
	padding-inline: 20px;
	margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}

nav {
	width: 100%;
}

.menu_mobile {
	padding: 10px;
}

@media screen and (max-width: 600px) {
	.menu {
		display: none;
	}
}

@media screen and (min-width: 600px) {
	#menu_icon_li {
		display: none;
	}
}

footer {
	background-color: #191c21;
	padding-top: .008%;
	padding-bottom: .008%;
}

footer ul {
	list-style-type: none;
	color: white;
	background: #191c21;
	overflow: hidden;
}